body, html {
  font-family: "Rubik", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  min-width: 375px;
  color: #ffffff;
  background: #2e2e2e;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.blue-text {
  color: #a2cbe8;
}

.container {
  width: 325px;
  margin: 0 auto;
  padding: 0 25px;
}
@media (min-width: 768px) {
  .container {
    width: 678px;
    padding: 0 45px;
  }
}

.main-header__intro img {
  display: block;
  margin: 0 auto;
  transition: 1s;
}
.main-header__intro img:hover, .main-header__intro img:focus {
  transform: rotate(360deg);
  transition: ease-in 1s;
}

.main-header__title {
  font-size: 20px;
  line-height: 23px;
  font-weight: 400;
  text-align: center;
  position: relative;
  width: 305px;
  margin: 20px auto 40px auto;
}
.main-header__title::before {
  content: "";
  position: absolute;
  bottom: -20px;
  left: 40px;
  width: 240px;
  height: 1px;
  background: #ffffff;
  opacity: 0.06;
}

.main-header__low-text {
  color: #e8a2a2;
}

.main-header__info {
  letter-spacing: 1px;
  text-align: center;
  margin: 0;
}

.main-header__links-block {
  display: flex;
  justify-content: space-around;
  width: 325px;
  margin: 30px auto 0 auto;
}

.main-header__link {
  font-weight: 300;
  position: relative;
  text-decoration: none;
  color: #ffffff;
  letter-spacing: 1px;
  transition: 0.3s;
}
.main-header__link:hover, .main-header__link:focus {
  color: #8a8a8a;
  transition: 0.3s;
}
.main-header__link:hover::before, .main-header__link:focus::before {
  content: "";
  position: absolute;
  top: -15px;
  left: -25px;
  width: 110px;
  height: 50px;
  border: 1px solid #8a8a8a;
  border-radius: 50%;
}
.main-header__link:nth-of-type(2):hover::before, .main-header__link:nth-of-type(2):focus::before {
  left: -23px;
  width: 150px;
}

@media (min-width: 768px) {
  .main-header {
    background-image: url("../img/bg-noise.png");
    background-repeat: no-repeat;
  }

  .main-header__title {
    font-size: 27px;
    width: 620px;
    margin: 60px auto;
  }
  .main-header__title::before {
    left: 110px;
    width: 400px;
  }

  .main-header__info-block {
    display: grid;
    grid-template-columns: min-content min-content;
    grid-template-rows: min-content min-content;
  }
  .main-header__info-block img {
    height: 370px;
    grid-column: 2/3;
    grid-row: 1/3;
  }

  .main-header__info {
    font-size: 18px;
    width: 400px;
    height: min-content;
    grid-row: 1/2;
    margin-top: 70px;
  }

  .main-header__links-block {
    height: 21px;
    grid-column: 1/2;
    grid-row: 2/3;
    margin-top: -130px;
    margin-bottom: 0;
  }

  .main-header__link {
    font-size: 16px;
  }
}
.main-nav {
  position: relative;
}

.main-nav__nav-head {
  position: relative;
  display: flex;
  flex-direction: column-reverse;
}

.main-nav__logo {
  display: block;
  margin: 10px auto 0 auto;
  transition: 0.5s;
}
.main-nav__logo:hover, .main-nav__logo:focus {
  opacity: 0.7;
  transition: 0.5s;
}

.main-nav__button-block {
  width: 100%;
  height: 60px;
  transition: 0.1s;
}
.main-nav__button-block .main-nav__menu-button span:nth-of-type(1) {
  animation: ease 0.5s topClose forwards;
}
.main-nav__button-block .main-nav__menu-button span:nth-of-type(2) {
  animation: ease 0.5s mdlClose forwards;
}
.main-nav__button-block .main-nav__menu-button span:nth-of-type(3) {
  animation: ease 0.5s btmClose forwards;
}
.main-nav__button-block--open {
  background: #1c1c1c;
  opacity: 0.95;
  transition: 0.6s;
}
.main-nav__button-block--open .main-nav__menu-button span:nth-of-type(1) {
  animation: ease 0.5s topOpen forwards;
}
.main-nav__button-block--open .main-nav__menu-button span:nth-of-type(2) {
  animation: ease 0.5s mdlOpen forwards;
}
.main-nav__button-block--open .main-nav__menu-button span:nth-of-type(3) {
  animation: ease 0.5s btmOpen forwards;
}

.main-nav__menu-button {
  position: absolute;
  display: block;
  top: 35px;
  right: 30px;
  width: 30px;
  height: 25px;
  background-color: transparent;
  margin: 0;
  padding: 0;
  border: none;
  cursor: pointer;
}
.main-nav__menu-button span {
  position: relative;
  display: block;
  width: 100%;
  height: 2px;
  background-color: #ffffff;
  border-radius: 30px;
}
.main-nav__menu-button span + span {
  margin-top: 8px;
}

.main-nav__list {
  letter-spacing: 2px;
  text-align: right;
  position: absolute;
  top: 60px;
  left: 0;
  right: 0;
  margin: 0;
  padding: 30px 20px 40px 0;
  list-style: none;
  opacity: 0.95;
  z-index: 3;
  transform: translateY(-135%);
  transition: 0.5s;
}
.main-nav__list--open {
  background: #1c1c1c;
  opacity: 0.95;
  transform: translateY(0);
  transition: 0.5s;
}

.main-nav__item a {
  font-size: 16px;
  text-decoration: none;
  color: #ffffff;
  transition: 0.5s;
}
.main-nav__item a:hover, .main-nav__item a:focus {
  color: #e8cca2;
  transition: 0.5s;
}
.main-nav__item + .main-nav__item {
  margin-top: 15px;
}

@media (min-width: 768px) {
  .main-nav {
    width: 678px;
    display: flex;
    justify-content: space-between;
    padding: 35px 45px 0 45px;
    margin: 0 auto;
  }

  .main-nav__nav-head {
    width: 188px;
  }

  .main-nav__button-block {
    display: none;
  }

  .main-nav__logo {
    align-self: flex-start;
    margin: 0;
  }

  .main-nav__list {
    width: 430px;
    position: static;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: center;
    column-gap: 20px;
    background: transparent;
    padding: 0;
    transform: translateY(0);
    border-bottom: 1px solid #8a8a8a40;
    opacity: 1;
  }

  .main-nav__item:last-of-type {
    display: none;
  }
  .main-nav__item + .main-nav__item {
    margin-top: 0;
  }
  .main-nav__item a {
    font-size: 13px;
  }
}
.advantages {
