}

.advantages__low-price {
  position: relative;
  width: 311px;
  background-image: url("../img/ellipse-bg.svg");
  background-repeat: no-repeat;
  background-size: 234px 234px;
  background-position: 50% 50%;
  margin-top: 85px;
  padding: 60px 0;
}
.advantages__low-price::before {
  content: "";
  position: absolute;
  bottom: -35px;
  left: 155px;
  width: 4px;
  height: 46px;
  background-image: url("../img/arrow-btm.svg");
  background-repeat: no-repeat;
}

.advantages__quality {
  position: relative;
  width: 311px;
  background-image: url("../img/ellipse-bg.svg");
  background-repeat: no-repeat;
  background-size: 234px 234px;
  background-position: 50% 50%;
  padding: 60px 0;
}
.advantages__quality::before {
  content: "";
  position: absolute;
  bottom: -35px;
  left: 155px;
  width: 4px;
  height: 46px;
  background-image: url("../img/arrow-btm.svg");
  background-repeat: no-repeat;
}

.advantages__manufactured {
  width: 311px;
  background-image: url("../img/ellipse-bg.svg");
  background-repeat: no-repeat;
  background-size: 234px 234px;
  background-position: 50% 50%;
  padding: 60px 0;
}

@media (min-width: 768px) {
  .advantages {
    background-image: url("../img/bg-noise.png");
    background-repeat: no-repeat;
  }

  .advantages__info-block {
    flex-direction: row;
    flex-wrap: wrap;
    width: 678px;
    column-gap: 67px;
    margin-top: 40px;
  }

  .advantages__low-price {
    flex-basis: 45%;
    margin-top: 0;
  }
  .advantages__low-price::before {
    top: 110px;
    left: 330px;
    transform: rotate(-90deg);
  }

  .advantages__quality {
    flex-basis: 45%;
  }
  .advantages__quality::before {
    left: 70px;
    transform: rotate(30deg);
  }

  .advantages__manufactured {
    position: relative;
    flex-basis: 45%;
    margin: 0 auto;
  }
  .advantages__manufactured::before {
    position: absolute;
    top: -27px;
    left: 35px;
    content: "";
    width: 4px;
    height: 46px;
    background-image: url("../img/arrow-btm.svg");
    background-repeat: no-repeat;
    transform: rotate(150deg);
  }
}
